import React from 'react'
import { useGlobalContext } from '../context/GlobalState'


const RightCol = ( { customFields } ) => {

  const { state } = useGlobalContext()

  return <div  className="product-box-designer__right-col" >
    <div className="col-wrap">
      <div className="product-box-cava">
        <div className="product-box-preview">
          <figure className="product_item_thumbnail">
          { state.coverType == 'hide' ? '' : 
          state.coverType == 'image' ? <img src='../wp-content/uploads/woocommerce-placeholder.png' className="woocommerce-placeholder wp-post-image" /> :
          state.coverType == 'author' ? <div className="product-box-preview__author" >
            <img src="../wp-content/uploads/woocommerce-placeholder.png" className="product-box-preview__avatar" alt="Avatar" />
            <div className="product-box-preview__header-info">
              <h3 className="product-box-preview__display-name">Display name </h3>
              <span className="product-box-preview__email">email@Email.ex</span>
            </div>
          </div> : <img src='../wp-content/uploads/woocommerce-placeholder.png' className="woocommerce-placeholder wp-post-image" /> }
          </figure>
          { state.hideContent ? '' :
            <div className="wd-product__content">
              <div className="wd-product__head">
                <div className="wd-product__before-title">
                  { state.beforeTitle.map(field => <div className="product_item_details__meta">{ field.displayText.replace( '{field}', customFields.filter( customField => customField.id == field.slug )[0]?.name ) } </div> ) }
                </div>
                <div className="wd-product__title">
                  <h2  style={{fontSize: '20px'}}><a href='#'>{ state.title.type == 'title' ? 'Prdoct Title' : state.title.type == 'short-description' ? 'Short Description' : state.title.type == 'address' ? 'Adress' : '' }</a></h2>		
                </div>
                <div className="wd-product__after-title">
                  { state.afterTitle.map(field => <div className="product_item_details__meta">{ field.displayText.replace( '{field}', customFields.filter( customField => customField.id == field.slug )[0]?.name ) } </div> ) }
                </div>
              </div>
              <div className="wd-product__details">
                { state.details.map(field => <div className="product_item_details__meta">{ field.displayText.replace( '{field}', customFields.filter( customField => customField.id == field.slug )[0]?.name )} </div> ) }
              </div>
              <div className="wd-product__bottom">
                <div className="wd-product__bottom__left" >
                  { state.bottom.left.slug == 'hide' ? '' : state.bottom.left.slug == 'price' ? <Price /> : <AddToCart /> }
                </div>
                <div className="wd-product__bottom__right" >
                  { state.bottom.right.slug == 'hide' ? '' : state.bottom.right.slug == 'price' ? <Price /> : <AddToCart /> }
                </div>
              </div>
            </div>
          }
        </div>
      </div>
    </div>
  </div>
}

const Price = () => {
  return <>
    <span className="auction-price starting-bid" data-auction-id="11415" data-bid="" data-status="future"><span className="starting auction">Starting bid:</span> 
    <span className="woocommerce-Price-amount amount"><span className="woocommerce-Price-currencySymbol">$</span>0.00</span></span>
  </>
}

const AddToCart = () => {
  return <>
    <button className="button button-primary" href="#">Add to cart</button>
  </>
}
 
export default RightCol